.wrapper {
  background-color: rgba(0, 0, 0, 0.836);
}
.content {
  padding: 50px 0;
  max-width: 1000px;
  margin: 0 auto;
}
.tabs {
  margin: 0px 0px 50px 0px;
}
.tabs__items {
  display: flex;
}
.tabs__item {
  flex: 0 1 33.333%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
  span{
    position: relative;
    z-index: 2;
  }

}
.tabs__body {
}
.tabs__block {
  position: relative;
  padding: 20px;
  font-size: 18px;
  line-height: 24px;
  display: none;
  color: #fff;
}
.tabs__block:before{
  content:"";
  height: 50px;
  width: 33.333%;
  position: absolute;
  top: -50px;
}

.tabs__block:nth-child(1){
  background-color: #eb8698;
}
.tabs__block:nth-child(1):before{
  background-color: #eb8698;
  left: 0;
}
.tabs__block:nth-child(2){
  background-color: #9278aa;
}
.tabs__block:nth-child(2):before{
  background-color: #9278aa;
  left: 33.333%;
}
.tabs__block:nth-child(3){
  background-color: #97c67d;
}
.tabs__block:nth-child(3):before{
  background-color: #97c67d;
  right:0;
}
.tabs__block:target{
  display: block;
}

.text {
  font-size: 18px;
  line-height: 24px;
  color: #fff;
}

@media (max-width: 600px){
  .tabs__item span{
    display: block;
    width: 100px;
    white-space: nowrap;
    text-align: center;
    overflow: hidden;
    font-size: 14px;
    text-overflow: ellipsis;
  }
}